strong {
	font-weight: 700;
}

.has-x-large-font-size {
	line-height: var(--wp--custom--line-height--x-large);
}
.has-large-font-size {
	line-height: var(--wp--custom--line-height--large);
}
.has-small-font-size {
	line-height: var(--wp--custom--line-height--small);
}

.has-brand-charcoal-background-color {
	* {
		color: var(--wp--preset--color--brand-dust);
	}
}

@for $i from 1 through 6 {
	h#{$i} {
		&.has-x-large-font-size {
			font-size: calc(var(--wp--custom--font-size--heading-#{$i}) * 1.3) !important;
		}
		&.has-large-font-size {
			font-size: calc(var(--wp--custom--font-size--heading-#{$i}) * 1.15) !important;
		}
		&.has-medium-font-size {
			font-size: var(--wp--custom--font-size--heading-#{$i}) !important;
		}
		&.has-small-font-size {
			font-size: calc(var(--wp--custom--font-size--heading-#{$i}) * 0.85) !important;
		}	
		.has-brand-charcoal-background-color & {
			color: #fff;
		}
	}
}

.frm_style_formidable-style.with_frm_style {
	--border-radius: 0;
	--border-color: var(--wp--preset--color--brand-yellow);
	--font: var(--wp--preset--font-family--gotham-narrow);
	--field-font-size: 1rem;
	--field-border-width: 0 0 1px 0;
	--field-pad: 10px 4px;
	--border-color-active: transparent;
	padding-left: 8px;
	padding-right: 8px;
	select {
		padding-left: 0;
		// color: #A19A94;
		&::placeholder {
			color: #A19A94;
		}
	}
	input[type=text], 
	input[type=password], 
	input[type=email], 
	input[type=number], 
	input[type=url], 
	input[type=tel], 
	input[type=file], 
	input[type=search], 
	textarea,
	select {
		resize: none;
		&::placeholder {
			color: #A19A94;
		}

	}
	.frm_form_fields > fieldset {
		padding-bottom: 0;
	}
	.frm_form_field {
		&:has(> .frm_submit) {
			margin-bottom: 0;
		}
	}
	.frm_submit {
		button.frm_button_submit {
			margin: 0;
			background: #38332F;
			width: 100%;
			text-align: left;
			padding: 10px 16px;
			border: 0 none;
			font-size: 1rem;
			&::after {
				content: "";
				display: inline-block;
				width: 10px;
				height: 12px;
				background: var(--wp--preset--color--brand-yellow);
				clip-path: path("M8.16868 4.09641L1.30121 11.0121L0 9.71087L6.91566 2.8193H2V0.987976H10V9.01207H8.16868V4.09641Z");
				margin-left: 15px;
				transition: all .2s;
			}
			@media (hover: hover) {
				&:hover {
					&::after {
						transform: translate(2px, -2px);
					}
				}
			}
		}
	}
}