.wp-block-display-categories-grid {
	ul {
		list-style: none;
		padding-left: 0;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 32px;
		li {
			.wp-block-post-featured-image {
				&:not(:has(img)) {
					background: url(./images/checker.png);
					border-radius: 24px;
				}
				img {
					border-radius: 24px;
				}
			}
		}
	}
}